import React from "react";
import { DiscussionEmbed } from "disqus-react";
import Router from "next/router";

import Page from "./index";
import PostedOn from "./components/PostedOn";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { ButtonGroup, Button } from "reactstrap";

export default function BlogPage({ children, date, title }) {
  const bottom = React.useRef(null);
  const onScrollToBottom = () => {
    if (bottom.current) {
      bottom.current.scrollIntoView({ behavior: "smooth", block: "start" });
    }
  };

  return (
    <Page title={title}>
      <div>
        <span className="float-left">
          <PostedOn date={date} />
        </span>
        <span className="float-right">
          <ButtonGroup>
            <Button
              onClick={() => Router.push("/blog")}
              size="sm"
              variant="primary"
            >
              <FontAwesomeIcon icon="arrow-left" />
            </Button>
            <Button onClick={onScrollToBottom} size="sm" variant="primary">
              <FontAwesomeIcon icon="arrow-down" />
            </Button>
          </ButtonGroup>
        </span>
        <span className="clearfix" />
      </div>
      <br />
      {children}
      <br />
      <br ref={bottom} />
      <DiscussionEmbed
        shortname="sushantdhiman-github-io"
        config={{
          title,
        }}
      />
    </Page>
  );
}
